import * as React from 'react';
import { Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import Plot2Axis from '../../content-parts/Plot2Axis';

type WorkProps = {
    taskId: number,
    stationId: string,
    queueId: number,
    currentStep: number,
    plotData: IRealTimeData[],
    counterTenthSecond: number,
    interval: number
}

interface IRealTimeData {
    temperature: number,
    power: number
}

class WorkStep3 extends React.PureComponent<WorkProps> {
    constructor(props: WorkProps) {
        super(props);
    }

    public render() {
        if (this.props.currentStep !== 3) {
            return null
        }

        return (
            <Row className="mb-2">
                <Card>
                    <CardBody>
                        <CardText>
                            Temperature:{' '}
                            <span className="temp-value"> {Math.floor(this.props.plotData[this.props.plotData.length - 1].temperature)} C</span>
                        </CardText>
                        <CardText className="pb-2">
                            Power:{' '}
                            <span className="power-value">{Math.floor(this.props.plotData[this.props.plotData.length - 1].power)} %</span>
                        </CardText>
                        <CardText>
                            <Plot2Axis
                                plotData={this.props.plotData}
                                counterTenthSecond={this.props.counterTenthSecond}
                                interval={this.props.interval}
                            />
                        </CardText>
                    </CardBody>
                </Card>
            </Row>
        );
    }
};

export default WorkStep3;
